#navbar {
    position: fixed;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    padding-top: 20px;
    padding-left: 5%;
    padding-right: 5%;
    background: none;
    border: none;
    -webkit-box-shadow: none;
            box-shadow: none;
    z-index: 1000;
}

#navbar img {
    -ms-flex-item-align: center;
        align-self: center;
}

.navbar-item-text {
    text-transform: uppercase !important;
    font-family: 'Nexa Light';
    letter-spacing: 0.1em;
}

/* !important tags to overwrite the Semantic UI defaults */
.navbar-item {
    background-color: #0e0e0e !important;
    margin: 10px 0 10px 30px;
    font-size: 1em;
    padding: 0.8em 0.9em 0.8em 1.2em !important;
    letter-spacing: 0.3em;
    color: #f1f1f1 !important;
    height: 3em;
}

.navbar-logo-mobile {
    margin: 20px auto;
}

.ui.visible.sidebar {
    width: 50% !important;
}



