/* generic stuff and for desktop screens*/
.row {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	margin: 10px 5px;
	-ms-flex-direction: row !important;
	    flex-direction: row !important;
}

.text-field:first-child {
	margin-left: 0px !important;
}

.text-field {
	margin: 0px 10px !important;
}

.text-field:last-child {
	margin-right: 0px !important;
}

.last-name {
	-ms-flex: 5 1;
	    flex: 5 1;
}

.middle-initial {
	-ms-flex: 1 1;
	    flex: 1 1;
}

.first-name {
	-ms-flex: 7 1;
	    flex: 7 1;
}

.nickname {
	-ms-flex: 5 1;
	    flex: 5 1;
}

.student-number {
	-ms-flex: 3 1;
	    flex: 3 1;
}

.birthday {
	-ms-flex: 3 1;
	    flex: 3 1;
}

.email {
	-ms-flex: 6 1;
	    flex: 6 1;
}

.contact-number {
	-ms-flex: 6 1;
	    flex: 6 1;
}

.address {
	-ms-flex: 12 1;
	    flex: 12 1;
}

.dropdown-div {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-flow: column;
	    flex-flow: column;
}

.dropdown {
	-ms-flex: 1 1;
	    flex: 1 1;
	margin-bottom: 10px;
}

.dropdown-label {
	text-align: left;
	color: rgba(0, 0, 0, 0.54);
	padding: 0;
	font-size: 1rem;
	font-family: "Roboto", "Helvetica", "Arial", sans-serif;
	line-height: 1;
	-webkit-transform: translate(0, 1.5px) scale(0.75);
	    -ms-transform: translate(0, 1.5px) scale(0.75);
	        transform: translate(0, 1.5px) scale(0.75);
	-ms-flex-item-align: start;
	    align-self: flex-start;
	margin-bottom: 5px;
}

.personal-info-container {
	-ms-flex-flow: row !important;
	    flex-flow: row !important;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	margin-bottom: 30px;
	width: 85% !important;
}

.personal-info-form {
	max-width: 80%;
	margin: 0px 40px;
}

.glitter-pics {
	height: 60vh;
	display: -ms-flexbox;
	display: flex;
	min-width: 120px;
	-ms-flex-flow: column;
	    flex-flow: column;
	-ms-flex-pack: justify;
	    justify-content: space-between;
}

.glitter-pics > img {
	min-width: 70px !important;
	margin: 5px 5px 25px 5px;
}

.basic-info-container {
	margin-bottom: 10px;
}

/* for small desktop screens */
@media only screen and (max-width: 900px) {
	.glitter-pics {
		display: none;
	}
}

/* for mobile phone screens */
@media only screen and (max-width: 768px) {
	.row {
		-ms-flex-direction: column !important;
		    flex-direction: column !important;
		-ms-flex-align: stretch;
		    align-items: stretch;
		margin: 0px;
	}

	.ui.container.personal-info-container {
		margin-top: 20px;
	}

	.text-field:first-child {
		margin-left: 10px !important;
	}

	.text-field {
		margin-bottom: 5px !important;
	}

	.text-field:last-child {
		margin-right: 10px !important;
	}

}



