h3 {
    margin-top: 0px;
}

h1.page-title {
    font-family: 'Nexa Bold';
    font-size: 45px;
    margin-bottom: 0px;
}

.ui.container.page-container {
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    min-height: 100vh;
}

.section-footer {
    position: absolute;
    bottom: 0px;
    width: 100%;
    text-align: center;
    margin-bottom: 50px;
    margin-top: auto;
}

@media only screen and (max-width: 768px) {
    .section-footer {
        display: none;
    }
}


